<template>
    <div class="mainBox login">
        <el-col :span="6" class="loginFormBox">
            <el-card>
                <el-col>
                    <h2 class="diyFont1" style="font-size: 30px;">T o o l <br>s y s t e m</h2>
                </el-col>
                <el-form label-width="60px" @submit.native.prevent>
                    <el-form-item label="  :">
                        <el-input v-model="userName" @keyup.enter.native="userLogin" suffix-icon="el-icon-service" autofocus="true"></el-input>
                    </el-form-item>
                    <el-form-item label="  :">
                        <el-input v-model="passWord" @keyup.enter.native="userLogin" suffix-icon="el-icon-view" type="password"></el-input>
                    </el-form-item>
                </el-form>
                <el-col class="buttonBox">
                    <el-button type="success" size="small" @click="userLogin()">&nbsp;  <i
                            class="el-icon-arrow-right"></i></el-button>
                    <el-button type="info" size="small" @click="Tourist"><i class="el-icon-upload el-icon--right"></i>
                    </el-button>
                </el-col>
            </el-card>
        </el-col>

    </div>
</template>

<script>
    export default {
        name: "login",
        data() {
            return {
                userName: '',
                passWord: '',
            };
        },
        mounted() {
        },
        watch: {
        },
        methods: {
            userLogin: function () {
                if(this.userName == '' && this.passWord == ''){
                    this.$notify.error({
                        title: '',
                        message: '!'
                    });
                    return
                }
                if (this.userName == 'zhaoqian' && this.passWord == 'zhaoqian') {
                    this.$ls.set('user','zhaoqian')
                    this.jump('layout')
                } else {
                    this.$notify.error({
                        title: '',
                        message: '!'
                    });
                }
            },
            Tourist:function(){
                    this.$ls.set('user','')
                this.jump('layout')
                this.$message(' []');
            }
        }
    };
</script>


